











































































































.post__container {
  min-height: 50vh !important;
}

.post__picture-container {
  min-height: 560px;
}

.post__picture {
  height: 560px;
  min-height: 560px;
  max-width: 100%;
  overflow: hidden;
  background: { 
    size: cover;
    repeat: no-repeat;
    position: center;
  }
} 

.post__user-info-container {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;

}

.post__user-info {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;

  img { 
    border-radius: 69%;
    height: 42px;
    width: 42px;
  }

  p {
    margin: 0 0 0 8px;
  }
}

.post__row{
  margin-bottom: 18px;
}

